.passcode-page {
  font-family: 'Montserrat', sans-serif; }
  .passcode-page p {
    font-weight: bold;
    text-align: center; }
  .passcode-page .time {
    margin-top: 60px;
    color: #ea5535;
    font-size: 16px;
    margin-top: 40px;
    margin-bottom: 0; }
  .passcode-page .title {
    font-size: 16px;
    margin: 10px 0 10px;
    color: #363636; }
  .passcode-page .name {
    color: #008eff;
    font-size: 16px;
    margin-top: 30px;
    margin-bottom: 0;
    display: flex; }
    .passcode-page .name span {
      flex: 1;
      text-align: center; }
  .passcode-page .tip {
    color: #343434;
    font-size: 16px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px; }
    .passcode-page .tip .tip-box p {
      margin: 0;
      text-align: left; }
  .passcode-page .back-btn {
    color: #fff;
    width: 200px;
    padding: 8px 24px;
    font-size: 14px;
    text-align: center;
    margin: 0 auto 20px;
    border-radius: 4px;
    background-color: #07c160; }
  .passcode-page img {
    width: 50%;
    display: block;
    margin: 0 auto; }
  .passcode-page .nav {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0; }
    .passcode-page .nav img {
      width: 100%; }
